.step {
  @apply list-none
   text-slate-600 
   uppercase 
   flex-1 
   text-xs 
   relative 
   py-0 
   px-2 
   font-medium 
   before:content-[attr(before)] 
   before:w-6 
   before:h-6 
   before:inline-block 
   before:text-xs 
   before:rounded-full 
   before:relative 
   before:mt-0 
   before:mx-0  
   before:mr-4  
   before:mb-[20px] 
   before:z-1 
   before:text-slate-600 
   dark:before:text-slate-300 
   before:bg-slate-100 
   dark:before:bg-slate-700 
   before:ring-1 
   before:ring-slate-200 
   dark:before:ring-slate-700 
   before:ring-offset-2 
   before:ring-offset-slate-50 
   dark:before:ring-offset-slate-800 
   before:leading-6 
   before:text-center
   md:before:block
   md:before:mr-auto
   md:before:ml-auto;
}
.step span {
  @apply text-slate-400;
}

.step-inner {
  @apply after:w-full 
 after:border 
 after:border-dashed 
 after:border-slate-300 
 dark:after:border-slate-700/40 
 after:absolute 
 after:top-3 
 after:-left-[50%] 
 after:-z-1
 after:hidden
 md:after:block;
}

.complete {
  @apply before:bg-primary-100 before:text-primary-500 before:ring-primary-100 dark:before:bg-slate-900 dark:before:ring-slate-700;
}

.continuous {
  @apply before:bg-primary-500 before:text-white before:ring-primary-500 dark:before:bg-primary-500 dark:before:text-white dark:before:ring-primary-500;
}
.continuous span {
  @apply text-primary-500;
}

.finish {
  @apply before:bg-green-500 before:text-white before:ring-green-500;
}
.finish span {
  @apply text-green-500;
}

